import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { HttpModule } from '@angular/http';
import { ProfileRoutingModule } from './profile-routing.module';

import { MainProfileComponent } from './main-profile/main-profile.component';
import { MyProfileComponent } from './my-profile/my-profile.component';
import { MySalesComponent } from './my-sales/my-sales.component';
import { MyOrdersComponent } from './my-orders/my-orders.component';
import { MyMediaComponent } from './my-media/my-media.component';
import { MySubscriptionsComponent } from './my-subscriptions/my-subscriptions.component';
import { MyFriendsComponent } from './my-friends/my-friends.component';
import { PhotoComponent } from './my-media/photo/photo.component';
import { VideoComponent } from './my-media/video/video.component';
import { ProfileService } from './profile_service';
import { SocialService } from './social.service';
import { FileSystemResourceService } from './file-system-resource.service';
import { EditProfileComponent } from './edit-profile/edit-profile.component';

import { TranslateModule, TranslateLoader } from "@ngx-translate/core";
import { ApiModule } from "../../common/api"
import { AuthModule } from "../../common/auth"
import { VoucherModule } from "../../common/voucher"
import { PostModule } from "../../common/components/post"
import { CardModule } from "../../common/components/card"
import { ReactiveFormsModule } from '@angular/forms';
import { SliderModule } from '../slider';
import { NglModule } from 'ng-lightning/ng-lightning';
import { FlexLayoutModule } from '@angular/flex-layout';

import { MatDatepickerModule } from '@angular/material/datepicker';
import { MatFormFieldModule, MatNativeDateModule } from '@angular/material';
import { MatInputModule } from '@angular/material';
import { FormsModule } from '@angular/forms';

@NgModule({
  imports: [
    HttpModule,
    CommonModule,
    FormsModule,
    ProfileRoutingModule,
    ReactiveFormsModule,
    ApiModule,
    VoucherModule,
    CardModule,
    AuthModule,
    NglModule,
    PostModule,
    FlexLayoutModule,
    MatDatepickerModule,
    MatNativeDateModule,
    MatInputModule,
    MatFormFieldModule,
    SliderModule
  ],
  providers: [ProfileService, SocialService, FileSystemResourceService],
  declarations: [MainProfileComponent, EditProfileComponent, MyProfileComponent, MySalesComponent, MyOrdersComponent, MyMediaComponent, MySubscriptionsComponent, MyFriendsComponent, PhotoComponent, VideoComponent]
})
export class ProfileModule { }



