import { ApiRequest, ApiBuilder } from '../../common/api'
import { AuthService } from '../../common/auth/';
import { Injectable } from '@angular/core';
import { HttpClient, HttpRequest, HttpHeaders } from '@angular/common/http';

@Injectable()
export class SocialService {

  private requestContact: ApiRequest<any>;

  constructor(private apiBuilder: ApiBuilder, private authService: AuthService) {
    this.requestContact = apiBuilder.defaultOptions()
      .path("social/")
      .build<any>();
  }

  getTimeline(data: any) {
    return this.requestContact.post("timeline.s", data);
  }
  saveStory(description: any) {
    return this.requestContact.post("story/save.s", {
      "storyTypeCode": "R",
      "name": "4",
      "externalLink": "4",
      "latitude": 2927391273,
      "longitude": "4333333",
      "description": description,
      "title": "untitled",
      "disabled": "false",
      "categoryCode": "4"
    }
    );
  }
  storyTypes() {
    return this.requestContact.post("story/types.s");
  }

}



