import { ApiRequest, ApiBuilder } from '../../common/api'
import { AuthService } from '../../common/auth/';

import { Injectable } from '@angular/core';
import { HttpClient, HttpRequest, HttpHeaders } from '@angular/common/http';


@Injectable()
export class ProfileService {

  private requestContact: ApiRequest<any>;

  constructor(private apiBuilder: ApiBuilder, private authService: AuthService) {
    this.requestContact = apiBuilder.defaultOptions()
      .path("profile/")
      .build<any>();
  }

  loadNotes() {
    return this.requestContact.post("notes");
  }

  submitNote(note: string) {
    return this.requestContact.post("notes", { note });
  }

  loadPosts() {
    return this.requestContact.post("posts");
  }

  loadComments(postId: string) {
    return this.requestContact.post("posts", { postId });
  }

  likePost(post: string) {
    return this.requestContact.post("notes", { post });
  }

}



