import { ApiRequest, ApiBuilder } from '../../common/api'
import { AuthService } from '../../common/auth/';
import { Injectable } from '@angular/core';
import { HttpClient, HttpRequest } from '@angular/common/http';
import { RequestOptions, Headers, Http } from '@angular/http';
import { Observable } from 'rxjs/Rx';
import { Context } from '../../services/model/context';

@Injectable()
export class FileSystemResourceService {

  private requestContact: ApiRequest<any>;

  constructor(private apiBuilder: ApiBuilder, private authService: AuthService, private http: Http) {
    this.requestContact = apiBuilder.defaultOptions()
      .path("fileSystemResource/")
      .build<any>();
  }

  public profilePictureUpload(file: File) {
    let progressObserver;
    let progress;
    progress = Observable.create(observer => {
      progressObserver = observer
    }).share();

    return Observable.create(observer => {
      let formData: FormData = new FormData();
      let xhr: XMLHttpRequest = new XMLHttpRequest();

      formData.append("image", file, file.name);

      xhr.onreadystatechange = () => {
        if (xhr.readyState === 4) {
          if (xhr.status === 200) {
            observer.next(JSON.parse(xhr.response));
            observer.complete();
          } else {
            observer.error(xhr.response);
          }
        }
      };

      xhr.upload.onprogress = (event) => {
        progress = Math.round(event.loaded / event.total * 100);
        progressObserver.next(progress);
      };

      xhr.open('POST', Context.API_URL + "fileSystemResource/profilePictureUpload.a", true);
      xhr.send(formData);
    });
  }

  public profilePictureUploadV2(file: File) {
    let formData: FormData = new FormData();
    formData.append('image', file);
    return this.requestContact.postFile("profilePictureUpload.a", formData)
  }
  public postUpload(directory, file: File) {
    let formData: FormData = new FormData();
    formData.append('directoryCode', directory);
    formData.append('image', file);
    return this.requestContact.postFile("upload.a", formData)
  }

}



