import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { MainProfileComponent } from './main-profile/main-profile.component';
import { MyProfileComponent } from './my-profile/my-profile.component';
import { EditProfileComponent } from './edit-profile/edit-profile.component';
import { MySalesComponent } from './my-sales/my-sales.component';
import { MyOrdersComponent } from './my-orders/my-orders.component';
import { MyMediaComponent } from './my-media/my-media.component';
import { MySubscriptionsComponent } from './my-subscriptions/my-subscriptions.component';
import { MyFriendsComponent } from './my-friends/my-friends.component';
import { PhotoComponent } from './my-media/photo/photo.component';
import { VideoComponent } from './my-media/video/video.component';
import { AuthGuard } from '../../common/auth';

const profileRoutes: Routes = [
  {
    path: '', component: MainProfileComponent, children: [
      {
        path: '', redirectTo: 'edit-profile', pathMatch: 'full',
      },
      {
        path: 'my-profile', component: MyProfileComponent
      },
      {
        path: 'edit-profile', component: EditProfileComponent,
        data: { isAuth: true },
        canActivate: [AuthGuard]
      },
      {
        path: 'my-sales', component: MySalesComponent
      },
      {
        path: 'orders', component: MyOrdersComponent
      },
      {
        path: 'media', component: MyMediaComponent, children: [
          {
            path: '', redirectTo: 'photos', pathMatch: 'full'
          },
          {
            path: 'photos', component: PhotoComponent
          },
          {
            path: 'videos', component: VideoComponent
          }
        ]
      },
      {
        path: 'subscriptions', component: MySubscriptionsComponent
      },
      {
        path: 'friends', component: MyFriendsComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(profileRoutes)],
  exports: [RouterModule]
})
export class ProfileRoutingModule { }


