import { Component, OnInit, OnDestroy } from '@angular/core';

import { VoucherService } from '../../../common/voucher/voucher.service'

@Component({
  selector: 'app-my-sales',
  templateUrl: './my-sales.component.html',
  styleUrls: ['./my-sales.component.css']
})
export class MySalesComponent implements OnInit, OnDestroy {

  alive = true;

  constructor(private voucherService: VoucherService) { }

  ngOnInit() {
  }

  getSales() {
    this.voucherService.getSales()
      .takeWhile(() => this.alive)
      .subscribe(() => {
      })
  }

  ngOnDestroy() {
    this.alive = false;
  }

}



