import { Component, OnInit, OnDestroy } from '@angular/core';
import { ProfileService } from '../profile_service';
import { SocialService } from '../social.service';
import { UserService } from '../../../common/user/';
import { UserprofileService } from '../../../common/user/userprofile.service';
import { User } from '../../../common/model/user';

import { FormBuilder, FormGroup, Validators, FormControl } from '@angular/forms';
import { subYears } from 'date-fns';

@Component({
  selector: 'app-edit-profile',
  templateUrl: './edit-profile.component.html',
  styleUrls: ['./edit-profile.component.css']
})
export class EditProfileComponent implements OnInit, OnDestroy {

  alive = true;
  informationForm: FormGroup;
  passwordForm: FormGroup;
  public moment: Date;
  public autoClose = true;
  public min = new Date(2017, 7, 9);
  public max = subYears(new Date(), 18);
  user: any = {};
  passwordError = false;
  profileError = false;
  passwordSuccess = false;
  passwordProfile = false;

  passwordRegex = new RegExp("^(((?=.*[a-z])(?=.*[A-Z]))|((?=.*[a-z])(?=.*[0-9]))|((?=.*[A-Z])(?=.*[0-9])))(?=.{7,})");

  constructor(
    private profileService: ProfileService,
    private userService: UserService,
    private fb: FormBuilder,
    private socialService: SocialService,
    private userprofileService: UserprofileService
  ) {

    this.informationForm = fb.group({
      'firstName': [null, Validators.compose([Validators.required, Validators.minLength(3), Validators.maxLength(50)])],
      'lastName': [null, Validators.compose([Validators.required, Validators.minLength(3), Validators.maxLength(50)])],
      'dateOfBirth': [null, Validators.compose([Validators.required])],
      'phoneNumber': [null, Validators.compose([Validators.required])],
    });
    this.passwordForm = new FormGroup({
      'oldPassword': new FormControl(null, [Validators.required]),
      'newPassword': new FormControl(null, [Validators.required, this.validatePassword.bind(this)]),
      'confirmPassword': new FormControl(null, [Validators.required, this.matchingPasswords.bind(this)])
    });
  }

  matchingPasswords(control: FormControl): { [s: string]: boolean } {
    if (control.value !== this.user.newPassword) {
      return { 'matchingPasswords': true }
    }
    return null;
  }

  validatePassword(control: FormControl): { [s: string]: boolean } {
    if (!control.value || !this.passwordRegex.test(control.value)) {
      return { 'password': true }
    }
    return null;
  }

  saveUserInfo() {
    console.log("informationForm", this.informationForm);
    this.userprofileService.setUserProfile([{ key: "userProfile", value: JSON.stringify(this.informationForm.value) }])
      .takeWhile(() => this.alive).subscribe((response) => {
        console.log("setUserProfile>>> ", response);
      });
  }

  changePassword() {
    if (!this.passwordForm.valid) {
      return;
    }
    let clearForm = () => {
      this.user.newPassword = "";
      this.user.oldPassword = "";
      this.user.confirmPassword = "";
      this.passwordForm.markAsPristine();
      this.passwordForm.markAsUntouched();
    }
    this.userService.changePassword(this.user.oldPassword,
      this.user.newPassword,
      this.user.confirmPassword)
      .takeWhile(() => this.alive).subscribe((user) => {
        this.passwordSuccess = true;
        setTimeout(_ => {
          this.passwordSuccess = false;
        }, 3000);
        clearForm();
      }, (error) => {
        this.passwordError = true;
        setTimeout(_ => {
          this.passwordError = false;
        }, 3000);
        clearForm();
      });
  }
  getUserSession() {
    this.userService.getUser().takeWhile(() => this.alive).subscribe((user) => {
      console.log("USER SESSION", user);
      this.user.firstName = user.firstName;
      this.user.lastName = user.lastName;
      this.user.dateOfBirth = user.dateOfBirth;
      this.user.phoneNumber = user.phoneNumber;
    });
  }

  getUserProfile() {
    this.userprofileService.getUserProfile().takeWhile(() => this.alive).subscribe((data) => {
      try {
        this.user = JSON.parse(data.userProfile);
      } catch (error) {

      }
      this.user.dateOfBirth = new Date(this.user.dateOfBirth);
      this.moment = new Date(this.user.dateOfBirth);
      console.log("USER==> ", this.moment);
      if (!this.user.firstName) {
        this.getUserSession();
      }

    });
  }

  ngOnInit() {
    this.getUserProfile();
  }

  ngOnDestroy() {
    this.alive = false;
  }

}



