import { Component, OnInit, OnDestroy } from '@angular/core';
import { FileSystemResourceService } from '../file-system-resource.service';
import { UserService } from '../../../common/user/';
import { UserprofileService } from '../../../common/user/userprofile.service';
import { MediaService } from '../../../common/media/media.service';
import { User } from '../../../common/model/user';
import { TranslateService } from '@ngx-translate/core';
import { Router } from '@angular/router';
import { DomSanitizer } from '@angular/platform-browser';


@Component({
  selector: 'app-main-profile',
  templateUrl: './main-profile.component.html',
  styleUrls: ['./main-profile.component.css']
})
export class MainProfileComponent implements OnInit, OnDestroy {

  private menuList: [{ title: string, path: string }];
  alive = true;
  mobile = false;
  loading = false;
  banner: any = [];
  user: User;
  userProfile: any;
  imageProfile: any;
  bannerProfile = 'profile-banner.jpg';

  constructor(private fileSystemResourceService: FileSystemResourceService,
    private userService: UserService,
    private translate: TranslateService,
    private router: Router,
    private userprofileService: UserprofileService,
    private mediaService: MediaService, private sanitizer: DomSanitizer
  ) {
    this.menuList = [
      // { title: "My Profile", path: "my-profile" },
      { title: "Information", path: "edit-profile" },
      // { title: "My Sales", path: "my-sales" },
      // { title: "Orders", path: "orders" },
      // { title: "Media", path: "media" },
      // { title: "Subscriptions", path: "subscriptions" },
      // { title: "Friends", path: "friends" }
    ]
  }
  getImage() {
    if (!this.userProfile || !this.userProfile.profilePicture) {
      return;
    }


    this.mediaService.getMedia(this.userProfile.profilePicture).takeWhile(() => this.alive).subscribe(
      result => {
        this.imageProfile = this.sanitizer.bypassSecurityTrustResourceUrl(result);
        this.loading = false;
      }
    );
  }

  images: Array<any>;
  loadSlider() {
    // let profile = this.translate.instant("Profile");
    if (window.screen.width <= 959) {
      this.bannerProfile = 'profile-banner-xs.jpg';
    } else {
      this.bannerProfile = 'profile-banner.jpg';

    }
    let bannerimg = this.banner[0];
    this.images = [
      {
        "sType": "div", "styles": { "width": "100%" }, "content": `
        <!--<div class="container-slide" style="background-image: url('/assets/images/${bannerimg}'); width:100%; background-repeat: no-repeat;background-attachment: initial;background-position: center; background-size:cover;">!-->
        <div class="container-slide">
      <div  class="slide-middle-txt animated">
      <h1>Panda <span>Profile</span></h1>
  </div>
  </div>
      ` }];
  }

  getUserProfile() {
    this.userprofileService.getUserProfile().takeWhile(() => this.alive).subscribe((response) => {
      try {
        this.user = JSON.parse(response.userProfile);
      } catch (error) {

      }

    });
  }

  ngOnInit() {
    if (window.screen.width <= 959) {
      this.mobile = true;
    }
    this.userService.tokenChanged.takeWhile(() => this.alive).subscribe((user) => {
      this.router.navigate(['/'])
    });
    // this.userService.getUser().takeWhile(() => this.alive).subscribe((user) => {
    //   this.user = user;
    // });
    this.getUserProfile();
    this.getUserImage();
    this.loadSlider();

    this.userprofileService.changedProfile.takeWhile(() => this.alive).subscribe((data) => {
      this.getUserProfile();
    });
  }
  getUserImage() {
    this.userService.getUserProfileDetail().takeWhile(() => this.alive).subscribe((user) => {
      this.userProfile = user;
      this.getImage();

    }, () => {
      this.loading = false;
    });
  }

  ngOnDestroy() {
    this.alive = false;
  }

  profilePictureUpload(fileInput: any) {
    this.loading = true;
    console.log("fileInput>>> ", fileInput);
    this.fileSystemResourceService.profilePictureUploadV2(fileInput.target.files[0]).subscribe(res => {
      this.getUserImage();

      console.log("profilePictureUpload>>> ", res);
    }, () => {
      this.loading = false;
    });
    // this.fileSystemResourceService.profilePictureUpload(fileInput.target.files[0]).subscribe(res => {
    //   console.log("profilePictureUpload>>> ", res);
    // });
  }
}



